      this.$set(this.editGoodsInfoForm, 'en_name', res.data.data.goods_name)
    },
    async changeColor(e) {
      const res = await this.postRequest('v1/changeThisInputsColor', {
        color_id: e,
        goods_id: this.editGoodsInfoForm.order_goods.goods_id,
      })
      this.$set(this.editGoodsInfoForm.order_goods, 'goods_img', res.data.data)
    },
    // 复制函数
    onError(e) {
      this.$message({
        message: '复制失败！',
        type: 'error',
      })
    },
    copyFun(e) {
      this.$message({
        message: '复制成功！',
        type: 'success',
      })
    },
    ///导入订单页面选择
    orderImport() {
      this.orderImportDialogVisible = true
    },
    //  改变表格高度按钮
    changeMainHeight() {
      this.tableHeight = '80vh'
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.is_urgent == 1) {
        return 'urgentBackgroundColor'
      } else {
        return ''
      }
    },
    handleSizeChange(val) {
      this.orderManagement.limit = val
      this.initData()
      // this.$store.commit('CHANGE_ORDER_LIMIT', val)
    },
    handleCurrentChange(val) {
      this.orderManagement.page = val
      this.initData()
      // this.$store.commit('CHANGE_ORDER_PAGE', val)
    },
    //导出亚马逊订单
    exportOrder() {
      let addShop = ''
      if (this.multipleSelection.length > 0) {
        // this.handleId(this.multipleSelection)
        addShop = this.multipleSelection.join(',')
        this.postRequest(`/v1/exportExcel?id=${addShop}`).then((res) => {
          if (res.data.code == '200') {
            window.location.href = res.data.data
          } else {
            this.$message({
              type: 'warning',
              message: '导出出现问题，请重试~',
            })
          }
        })
      } else {
        this.$message({
          message: '请选择需要导出的订单记录！',
          type: 'warning',
        })
      }
    },
    //更新最新物流信息
    updateLogisticsInfo() {
      let addShop = ''
      if (this.multipleSelection.length > 0) {
        // this.handleId(this.multipleSelection)
        // addShop = this.multipleSelectionId.join(',')
        addShop = this.multipleSelection.join(',')
        // console.log(addShop);

        // ;
      } else {
        this.$message({
          message: '请选择需要更新的订单！',
          type: 'warning',
        })
      }
    },
    //重新生成运单号
    generateWaybillNumber() {
      this.$message({
        message: '订单当前状态不允许重新生成运单，请联系管理员！',
        type: 'warning',
      })
    },
    //设为加急单
    SetUrgentOrder() {
      let addShop = ''
      if (this.multipleSelection.length > 0) {
        addShop = this.multipleSelection.join(',')
        this.postRequest(`/v1/orderEditurgent?order_id=${addShop}`).then(
          (res) => {
            if (res.data.code == '200') {
              this.$message({
                type: 'success',
                message: '设置成功~',
              })
              this.initData()
            } else {
              this.$message({
                message: '订单当前状态无需加急',
                type: 'warning',
              })
            }
          }
        )
      } else {
        this.$message({
          message: '请选择订单',
          type: 'warning',
        })
      }
    },
    //订单匹配
    orderMatching() {},
    // 订单拆分
    orderSplit() {
      let addShop = ''
      if (this.multipleSelection.length == 1) {
        addShop = this.multipleSelection.join(',')
        this.postRequest(`/v1/split?order_id=${addShop}`).then((res) => {
          if (res.data.code == '200') {
            this.$message({
              type: 'success',
              message: '订单拆分成功~',
            })
            this.initData()
          } else {
            this.$message({
              message: '订单拆分失败',
              type: 'warning',
            })
          }
        })
      } else if (this.multipleSelection.length > 1) {
        this.$message({
          message: '只能选择一条订单拆分',
          type: 'warning',
        })
      } else {
        this.$message({
          message: '请选择订单',
          type: 'warning',
        })
      }
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    //初始化数据
    initData(u_id = sessionStorage.getItem('selectUId')) {
      this.multipleSelection.length = 0
      this.loading = true
      this.postRequest(
        `/v1/orderStatusCate?u_id=${u_id}`,
        this.orderManagement
      ).then((res) => {
        console.log(res)
        if (res.data.code == '200') {
          this.tableData = res.data.data.data
          this.total = res.data.data.total
          this.loading = false
          for (var i = 0; i < this.tableData.length; i++) {
            this.$set(this.checked, 'main' + i, false)
            var obj = {}
            obj.height = 100 * this.tableData[i].goods_info.length + 'px'
            obj.paddingTop =
              (100 * this.tableData[i].goods_info.length - 15) * 0.5 + 'px'
            obj.borderLeft = '1px solid #ccc'
            var obj1 = {}
            obj1.height = 100 * this.tableData[i].goods_info.length + 'px'
            obj1.paddingTop =
              (100 * this.tableData[i].goods_info.length - 30) * 0.5 + 'px'
            obj1.borderLeft = '1px solid #ccc'
            var obj2 = {}
            obj2.height = 100 * this.tableData[i].goods_info.length + 'px'
            obj2.paddingTop =
              (100 * this.tableData[i].goods_info.length - 45) * 0.5 + 'px'
            obj2.borderLeft = '1px solid #ccc'
            var obj3 = {}
            obj3.height = 100 * this.tableData[i].goods_info.length + 'px'
            obj3.paddingTop =
              (100 * this.tableData[i].goods_info.length - 60) * 0.5 + 'px'
            obj3.borderLeft = '1px solid #ccc'
            var objBtn = {}
            objBtn.height = 100 * this.tableData[i].goods_info.length + 'px'
            objBtn.paddingTop =
              (100 * this.tableData[i].goods_info.length - 25) * 0.5 + 'px'
            objBtn.borderLeft = '1px solid #ccc'
            this.$set(this.tableHeightMainBtn, 'main' + i, objBtn)
            this.$set(this.tableHeightMain3, 'main' + i, obj3)
            this.$set(this.tableHeightMain2, 'main' + i, obj2)
            this.$set(this.tableHeightMain1, 'main' + i, obj1)
            this.$set(this.tableHeightMain, 'main' + i, obj)
          }
        }
      })
    },
    // 初始化所有物流
    async logisticsList() {
      let result = await this.api.logisticsList()

      this.ShippingList = result.data.data
      this.ShippingList.unshift({
        id: -1,
        courier_name: '全部',
      })
      this.ShippingList.push({
        id: 0,
        courier_name: '无',
      })
    },
    //获得国家数据
    initCountryCity() {
      this.postRequest(`/v1/goodsCoutry`).then((res) => {
        //  console.log(res.data.data);
        this.countryList = res.data.data
      })
    },

    // 获取开户站列表
    async getAccountList() {
      const res = await this.api.subaccountList()
      if (res.data.code == 200) {
        this.AccountList = res.data.data
        //  this.total = res.data.data.total;
      }
    },
    // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists()
      if (res.data.code == 200) {
        this.storeList = res.data.data
        if (this.storeList.length > 0) {
          this.uploadData.shopid = this.storeList[0].id
        }
      }
    },

    //表格单击main->mainBottom
    handleClickCell(val) {
      this.tableHeight = `80vh`
      this.$store.commit('CHANGE_ORDER_DATA', val)
    },
    //多选
    handleSelectionChange(val) {
      this.multipleSelection = []
      for (const key in val) {
        if (val.hasOwnProperty(key)) {
          const element = val[key]
          this.multipleSelection.push(element)
          for (const key in element.sub_goods) {
            if (element.sub_goods.hasOwnProperty(key)) {
              let id = element.sub_goods[key]
              this.multipleSelection.push(id)
            }
          }
        }
      }
      // console.log(this.multipleSelection);
    },
    handleId(data) {
      this.multipleSelectionId = []
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          const element = data[key]
          this.multipleSelectionId.push(element.id)
        }
      }
    },
  },
  computed: {
    cateId() {
      return this.$store.state.orderManagement.product_cate_id
    },
    shopStatus() {
      return this.$store.state.orderManagement.order_status
    },
    orderShinpingCode() {
      return this.$store.state.orderManagement.shipping_code
    },
    orderTime() {
      return this.$store.state.orderManagement.times
    },
    orderCountry() {
      return this.$store.state.orderManagement.country_id
    },
    orderLimit() {
      return this.$store.state.orderManagement.limit
    },
    orderPage() {
      return this.$store.state.orderManagement.page
    },
    orderSearchVal() {
      return this.$store.state.orderManagement.tracking_num
    },
    orderRefreshStatus() {
      return this.$store.state.orderRefreshStatus
    },
    changeUserId() {
      return this.$store.state.changeUserId
    },
  },
  watch: {
    // 'editGoodsInfoForm.order_goods.goods_id':function(newVal,oldVal){
    //   console.log(newVal,oldVal)
    //   this.$set(editGoodsInfoForm,'colors',newVal)
    //   this.editGoodsInfoForm.order_goods.color_id=null
    //   this.editGoodsInfoForm.order_goods.size_id=null
    // },
    multipleSelection: function (newVal, oldVal) {
      if (newVal.length == this.tableData.length) {
        this.checkedAllOrder = true
      } else {
        this.checkedAllOrder = false
      }
    },
    cateId: function (newVal, oldVal) {
      // sessionStorage.setItem('oldShopCate_id',newVal);//防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData()
    },
    shopStatus: function (newVal, oldVal) {
      sessionStorage.setItem('orderStatus', newVal) //防止用户刷新页面时因为cate_id被刷新掉而进行重复请求
      this.initData()
    },
    orderCountry: function (newVal, oldVal) {
      this.initData()
    },
    orderShinpingCode: function (newVal, oldVal) {
      this.initData()
    },
    orderTime: function (newVal, oldVal) {
      this.initData()
    },
    orderPage: function (newVal, oldVal) {
      this.initData()
    },
    orderLimit: function (newVal, oldVal) {
      this.initData()
      // this.checkedAllOrder = false
    },
    orderSearchVal: function (newVal, oldVal) {
      this.initData()
    },
    orderRefreshStatus: function (newVal, oldVal) {
      this.initData()
    },
    changeUserId: function (newVal, oldVal) {
      this.initData()
    },
  },
  components: {
    mainBottom,
    uploadExcel,
    uploadTxt,
    scrollButton,
  },
  created() {
    this.initData()
    this.getStoreList()
    this.getAccountList()
    this.logisticsList()
    this.initCountryCity()
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
  },
}
</script>
