/*订单管理main-bottom*/
<template>
  <div class="mainBottom">
    <el-dialog title="订单详情" :visible.sync="bottomShowStatus" width="90%">
    <div class="mainBottomLeft">
      <el-form :model="sizeForm" :rules="rules" ref="ruleForm" label-width="80px" size="mini" :inline-message="true">
         <el-form-item size="large" class="marginBottm5 lineHeight20 textLeft">
          (商品合计：<span class="colorRed">{{ sizeForm.goods_price }}</span> + 快递费用：<span class="colorRed">{{ sizeForm.shipping_price }}</span> = 订单总额：<span class="colorRed">{{ sizeForm.total_amount }}</span>)
        </el-form-item>
        <el-form-item label="送达地区" class="width58 disInlineBlock marginBottm5">
          <el-select v-model="sizeForm.country_alias" placeholder="送达国家" class="width40" disabled>
          </el-select>
          <el-select v-model="sizeForm.province" placeholder="送达城市" class="width58" disabled>
          <!-- <el-select v-model="sizeForm.province_alias" placeholder="送达城市" class="width58" disabled> -->
          </el-select>
        </el-form-item>
        <el-form-item label="城市" class="width40 disInlineBlock marginBottm5" prop="city_alias">
          <el-input v-model="sizeForm.city_alias"></el-input>
        </el-form-item>
        <el-form-item label="详细地址" class="width58 disInlineBlock marginBottm5" prop="address">
          <el-input v-model="sizeForm.address"></el-input>
        </el-form-item>
        <el-form-item label="邮编" class="width40 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.zipcode"></el-input>
        </el-form-item>
        <el-form-item label="收件人" class="width58 disInlineBlock marginBottm5" prop="consignee">
          <el-input v-model="sizeForm.consignee"></el-input>
        </el-form-item>
        <el-form-item label="电话" class="width40 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.mobile"></el-input>
        </el-form-item>
        <el-form-item label="订单号" class="width58 disInlineBlock marginBottm5">
          <el-input v-model="sizeForm.order_sn" disabled></el-input>
        </el-form-item>
        <el-form-item label="物流" class="width40 disInlineBlock marginBottm5">
          <el-select v-model="sizeForm.shipping_code_alias" disabled>
          </el-select>
        </el-form-item>
        <el-form-item label="订单备注" class="marginBottm5">
          <el-input type="textarea" v-model="sizeForm.user_note"></el-input>
        </el-form-item>
        <el-form-item size="large" class="marginBottm5 lineHeight20 textRight">
          <el-button type="warning" size="mini" @click="submitForm('ruleForm')">保存修改</el-button>
          <el-button type="danger" size="mini" @click="cancellationOfOrder">取消订单</el-button>
        </el-form-item>
      </el-form> 
    </div>

    <div class="mainBottomRight">
      <div class="pageClass">
        <el-button type="success" icon="el-icon-refresh" size="mini">刷新</el-button>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="pageSize"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total"
        ></el-pagination>
      </div>
      <el-table
        :data="tableData"
        style="width: 100%"
        border
        height="33vh">
        <el-table-column
          fixed
          header-align="center"
          align="center"
          type="index"
          min-width="50">
        </el-table-column>
        <el-table-column
          prop="date"
          label="图片"
          header-align="center"
          align="center"
          min-width="80">
          <template slot-scope="scope">
            <span v-show="scope.row.goods_imgs.length>0">
              <el-tooltip placement="right-start" effect="light">
                <div slot="content">
                  <img v-lazy="bigImgSrc" alt srcset class="bigImgCon">
                </div>
                <img v-lazy="scope.row.goods_imgs[0].path" class="ImgCon" alt srcset ref="user_image" @mouseover="mouseover(scope.row.goods_imgs)" @mousewheel="wheel(scope.row.goods_imgs)">
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="goods_name"
          header-align="center"
          align="center"
          label="商品标题"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="goods_weight"
          header-align="center"
          align="center"
          label="重量（KG）"
          min-width="120">
        </el-table-column>
        <el-table-column
          prop="msrp"
          header-align="center"
          align="center"
          label="单价"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="goods_num"
          label="数量"
          header-align="center"
          align="center"
          min-width="70">
        </el-table-column>
      </el-table>
    </div>
    </el-dialog>
  </div>
</template>

<script>
  let imgIndex = 0;//bigImg索引
  export default {
    data() {
      return {
        // 底部栏显示状态
        bottomShowStatus: false,
        total: 0,//该分类产品共有多少条数据（非共多少笔）
        currentPage: 1,//第几页
        pagerCount: 5,//
        pageSize: 50,//每页多少条
        sizeForm: {},
        bigImgSrc: '',//大图地址
        tableData: [],
        orderId: '',//订单ID
        order_sn: '',//运单号
        rules: {
          city_alias: [ { required: true, message: '请输入城市', trigger: 'blur' } ],
          address: [ { required: true, message: '请输入详细地址', trigger: 'blur' } ],
          consignee: [ { required: true, message: '请输入收件人', trigger: 'blur' } ],
        }
      };
    },
    methods: {
      // 关闭按钮
      closeButton(){
        this.$emit('changeMainHeight');
        this.$store.commit('CHANGE_ORDER_DATA', '');
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.saveOrder(this.sizeForm);
          } else {
            return false;
          }
        });
      },
      //保存修改
      saveOrder(data){
        this.postRequest(`/v1/OrderTosave`,data)
        .then(res => {
          if(res.data.code=='200'){
            this.$message({
              type: 'success',
              message: '修改成功'
            })
            this.$store.commit('CHANGE_ORDER_REFRESH_STATUS');
          }
        })
      },
      //取消订单前校验
      cancellationOfOrder(){
        this.$confirm('将取消该订单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.cancelOrder(this.order_sn);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '不取消'
          });          
        });
      },
      //取消订单
      cancelOrder(orderId,u_id=sessionStorage.getItem('selectUId')){
        if(orderId!=''){
          this.postRequest(`/v1/OrderCancellation?order_sn=${orderId}&u_id=${u_id}`)
          .then(res => {
            if(res.data.code=='200'){
              this.$message({
                type: 'success',
                message: '取消成功，费用退回已提交后台~'
              })
              this.$store.commit('CHANGE_ORDER_REFRESH_STATUS');
            }
          })
        }
      },
      wheel(imgValue,e=event) {
      this.bodyMouseWheel(e);
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = imgValue.length-1;
          // imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
          // ;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    mouseover(val){
      this.bigImgSrc = val[0].path;
    },
    bodyMouseWheel(e){
      e.preventDefault();
    },
      //改变每页多少条
      handleSizeChange(val) {
        if(this.orderId!=''){
          this.initTableData(this.orderId,val,this.currentPage);
        }else {
          return false;
        }
      },
    //改变页码
      handleCurrentChange(val) {
        if(this.orderId!=''){
          this.initTableData(this.orderId,this.pageSize,val);
        }else {
          return false;
        }
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      initTableData(id,limit,page){
        this.tableData = [];
        this.postRequest(`/v1/orderGoodsAobutLists?order_id=${id}&limit=${limit}&page=${page}`)
        .then(res => {
          this.tableData = res.data.data.data;
          this.total = res.data.data.total;
        })
      }
    },
    computed: {
      mainData(){
        return this.$store.state.orderManagementMainBottom;
      },
    },
    watch: {
      mainData: function(newVal,oldVal){
        if(newVal){
          this.bottomShowStatus = true;
          this.sizeForm = newVal;
          this.orderId = newVal.id;
          this.order_sn = newVal.order_sn;
        }else {
          this.bottomShowStatus = false;
        }
      },
      orderId: function(newVal,oldVal){
        this.initTableData(newVal,this.pageSize,this.currentPage);
      }
    },
  };
</script>

<style lang="scss" scoped>
.mainBottom {
  background-color: #f1f1f1;
  position: fixed;
  bottom: 0;
  .mainBottomLeft,.mainBottomRight {
    width: 49%;
