    width: 49.5%;
    height: 39.5vh;
    box-sizing: border-box;
    margin-right: 0.5%;
    padding: 20px 10px 0;
    border-radius: 5px;
    background-color: #fff;
  }
  .mainBottomRight {
    width: 49.5%;
    background-color: #fff;
    height: 38vh;
    border-radius: 5px;
  }
  .pageClass {
    text-align: left;
    height: 4vh;
    line-height: 4vh;
    padding-left: 5px;
  }
  .el-pagination {
    display: inline-block;
    float: right;
    line-height: 22px;
  }
}
</style>
