/*产品分类目录*/
<template>
  <div class="treeCon">
    <!-- <div class="iconProduct" @click="clickIconProductState">
      <i class="el-icon-d-arrow-right"></i>
      
    </div> -->
     <!-- v-show="iconProductState"  -->
    <el-collapse-transition>
      <div class="tree" @mouseover="mouseoverTree" @mouseout="mouseoutTree">
        <h3 style="padding: 10px 0 10px 15px;font-size: 14px;text-align:left;">产品分类目录</h3>
        <el-tree
          v-loading='treeLoading'
          :data="treeData"
          :props="defaultProps"
          :highlight-current="true"
          icon-class="iconfont icon_mulu"
          :default-expand-all="false"
          :expand-on-click-node="false"
          @node-click="handleNodeClick">
        </el-tree>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
export default {
  props: ['componentName'],
  data() {
    return {
      iconProductState: false,//产品列表显示状态
      // iconProductState: true,//产品列表显示状态
      treeData: [],//tree节点值
      defaultProps: {
        children: 'subcate',
        label: 'cate_name'
      },
      cate_id: '',//商品分类目录
      treeLoading: false
    };
  },
  methods: {
    handleNodeClick(data) {
      if(this.cate_id != data.id){
        this.cate_id = data.id;
        if(this.componentName=='OptionalCharges'){
          this.$store.commit('CHANGE_CATE_ID',data.id);
        }
        if(this.componentName=='CommodityManagement'){
          this.$store.commit('CHANGE_SHOP_PRODUCT_CATE_ID',data.id);
        }
        if(this.componentName=='OrderManagement'){
          this.$store.commit('CHANGE_ORDER_CATEID',data.id);
        }
        // ;
        // ;
      }else {
        return false;
      }
    },
    //修改产品列表目录状态
    clickIconProductState(){
      this.iconProductState = true;
    },
    mouseoutTree(){
      this.iconProductState = false;
      // this.iconProductState = true;
    },
    mouseoverTree(){
      this.iconProductState = true;
    },
    async initData(){
      this.treeLoading = true
      let res = await this.api.leftmenu();
      this.treeData.push({ id: 0, cate_name: "所有目录" });
      for (const key in res.data.data) {
        if (res.data.data.hasOwnProperty(key)) {
          const element = res.data.data[key];
          this.treeData.push(element);
        }
      }
      this.treeLoading = false
    },
  },
  created(){
     if(this.componentName=='OptionalCharges'){
          this.$store.commit('CHANGE_CATE_ID',0);
        }
        if(this.componentName=='CommodityManagement'){
          this.$store.commit('CHANGE_SHOP_PRODUCT_CATE_ID',0);
        }
        if(this.componentName=='OrderManagement'){
          this.$store.commit('CHANGE_ORDER_CATEID',0);
        }
  },
  beforeMount(){
    // this.$store.commit('CHANGE_SHOP_PRODUCT_CATE_ID',0);
    this.initData();
  }
};
</script>

